$poke-normal: #c5c5c5;
$poke-fighting: #d85f4f;
$poke-flying: #a1bbec;
$poke-poison: #b162c7;
$poke-ground: #cc753b;
$poke-rock: #c9bba4;
$poke-bug: #92bc2c;
$poke-ghost: #5f6dbc;
$poke-steel: #94a6b1;
$poke-fire: #e03b26;
$poke-water: #539ddf;
$poke-grass: #5fbd58;
$poke-electric: #ffb400;
$poke-psychic: #c734a2;
$poke-ice: #6bc5b6;
$poke-dragon: #ad9a66;
$poke-dark: #426075;
$poke-fairy: #e496dd;

$poke-types: (
  "colorless": $poke-normal,
  "normal": $poke-normal,
  "fighting": $poke-fighting,
  "flying": $poke-flying,
  "poison": $poke-poison,
  "ground": $poke-ground,
  "rock": $poke-rock,
  "bug": $poke-bug,
  "ghost": $poke-ghost,
  "metal": $poke-steel,
  "steel": $poke-steel,
  "fire": $poke-fire,
  "water": $poke-water,
  "grass": $poke-grass,
  "lightning": $poke-electric,
  "electric": $poke-electric,
  "psychic": $poke-psychic,
  "ice": $poke-ice,
  "dragon": $poke-dragon,
  "darkness": $poke-dark,
  "dark": $poke-dark,
  "fairy": $poke-fairy,
);

// Card
.border-radius-card {
  border-radius: $border-radius-card !important;
}

.money {
  color: $green;
}

.money-tag {
  color: $green;
  background: lighten(black, 5%);
  display: inline-block;
  padding: 4px 8px;
  display: inline-block;
  border-radius: $border-radius;
  font-weight: bold;
  font-size: 12px;
  position: relative;
  height: 22px;
  line-height: 1;
  box-shadow: $shadow-sheen;
  border: $border;
}

.button {
  &.small {
    .money-tag {
      margin-right: -4px;
    }
  }
  .money-tag {
    margin: -4px 0;
    margin-right: -4px;
    margin-left: auto;
  }
}

.card-image {
  border-radius: $border-radius-card;
  overflow: hidden;
  box-shadow: $shadow;
  position: relative;
  img {
    display: block;
  }
  &:after {
    border: $border;
    pointer-events: none;
    content: "";
    border-radius: $border-radius-card;
    width: 100%;
    height: 100%;
    box-shadow: $shadow-sheen;
    z-index: 10;
    position: absolute;
    border: $border;
    top: 0;
    left: 0;
  }
  @media (min-width: $screen-medium) {
    &.gfx {
      &:before,
      &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        opacity: 0.5;
        mix-blend-mode: color-dodge;
        transition: $transition;
        pointer-events: none;
      }
      &:before {
        background-position: 50% 50%;
        background-size: 300% 300%;
        background-image: linear-gradient(
          115deg,
          transparent 0%,
          rgb(0, 231, 255) 25%,
          transparent 47%,
          transparent 53%,
          rgb(255, 0, 231) 75%,
          transparent 100%
        );
        filter: brightness(0.5) contrast(1);
        z-index: 1;
      }
      &:after {
        background-image: url("/assets/stars.gif"), url("/assets/holo.png"),
          linear-gradient(
            125deg,
            #ff008450 15%,
            #fca40040 30%,
            #ffff0030 40%,
            #00ff8a20 60%,
            #00cfff40 70%,
            #cc4cfa50 85%
          );
        background-position: center;
        background-size: 160%;
        background-blend-mode: overlay;
        z-index: 2;
        mix-blend-mode: color-dodge;
        opacity: 1;
      }
    }
  }
  &.gfx-mobile {
    &:before,
    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      opacity: 0.5;
      mix-blend-mode: color-dodge;
      transition: $transition;
      pointer-events: none;
    }
    &:before {
      background-position: 50% 50%;
      background-size: 300% 300%;
      background-image: linear-gradient(
        115deg,
        transparent 0%,
        rgb(0, 231, 255) 25%,
        transparent 47%,
        transparent 53%,
        rgb(255, 0, 231) 75%,
        transparent 100%
      );
      filter: brightness(0.5) contrast(1);
      z-index: 1;
    }
    &:after {
      background-image: url("/assets/stars.gif"), url("/assets/holo.png"),
        linear-gradient(
          125deg,
          #ff008450 15%,
          #fca40040 30%,
          #ffff0030 40%,
          #00ff8a20 60%,
          #00cfff40 70%,
          #cc4cfa50 85%
        );
      background-position: center;
      background-size: 160%;
      background-blend-mode: overlay;
      z-index: 2;
      mix-blend-mode: color-dodge;
      opacity: 1;
    }
  }
}

.bg-image {
  position: absolute !important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: 100%;
  background-repeat: no-repeat;
  pointer-events: none;
  opacity: 0.4;
  z-index: 0 !important;
  &:before {
    content: "";
    position: absolute !important;
    left: 0;
    right: 0;
    top: 0;
    padding-bottom: 76%;
    background: linear-gradient(transparent, $main-bg);
    z-index: 2 !important;
  }
}
